.page {
    &-title {
        font-family: var(--font-family);
        font-size: var(--font-size-h1);
        font-weight: var(--font-weight-bold);
        color: var(--primary);
        line-height: var(--line-height-tight);
        margin-bottom: 40px;
    
        @include lg-down {
          font-size: var(--font-size-h2);
          margin-bottom: 30px;
        }
    
        @include md-down {
          font-size: var(--font-size-h3);
          margin-bottom: 24px;
        }
    }

    &__spacing {
        padding: 40px 0 80px;

        @include lg-down {
          padding: 30px 0 60px;
        }
    }
}

.actions__block {
    position: relative;
    overflow-x: unset;
    touch-action: unset;
    width: 100%;
    -webkit-overflow-scrolling: unset;
    margin-bottom: 0;
    padding: 0;

    @include lg-down {
      margin-bottom: 10px;
      padding: 20px 0;
      overflow-x: auto;
      touch-action: pan-x;
    }

    @include md-down {
      margin-bottom: 10px;
      padding: 20px 0;
      overflow-x: auto;
      touch-action: pan-x;
    }
}

.page-content {
    font-family: var(--font-family);
    font-size: var(--font-size-body);
    font-weight: var(--font-weight-regular);
    color: var(--gray-2);
    // line-height: var(--line-height-relaxed);
    // margin-bottom: 20px;
    display: flex;
    flex-direction: column;
    gap: 20px;

    h1, h2, h3, h4, h5, h6 {
        margin: 0;
        color: var(--primary);
    }

    h1 {
        font-size: var(--font-size-h1);
    }
    h2 {
        font-size: var(--font-size-h2);
    }
    h3 {
        font-size: var(--font-size-h3);
    }
    h4 {
        font-size: var(--font-size-h4);
    }
    h5 {
        font-size: var(--font-size-h5);
    }
    h6 {
        font-size: var(--font-size-h6);
    }

    p {
        margin: 0;
    }

    ul {
        margin: 0;
        padding-left: 0;
    }

    ul li {
        margin: 0;
    }

    img {
        width: 100%;
        height: 100%;
        max-height: 400px;
        aspect-ratio: 16/9;
        object-fit: cover;
        // margin-bottom: 20px;
        border-radius: 0;
        overflow: hidden;
    
        img {
          width: 100%;
          height: 100%;
          object-fit: cover;
        }
    
        @include lg-down {
            max-height: 300px;
        }
    
        @include md-down {
            max-height: 250px;
        }
      }
}